'use client';
import { useEffect, useState } from 'react';
import Link from 'next/link';

interface Product {
  id: string;
  name: string;
  price: number;
  imageUrl: string;
}

export default function ProductsPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [search, setSearch] = useState('');

  useEffect(() => {
    fetch('/api/products')
      .then((res) => res.json())
      .then((data) => setProducts(data));
  }, []);

  const filtered = products.filter((p) => p.name.toLowerCase().includes(search.toLowerCase()));

  return (
    <section className="py-10 px-4 md:px-8 bg-gradient-to-b from-pink-50 to-white min-h-[80vh]">
      <div className="max-w-7xl mx-auto">
        <div className="mb-10 text-center">
          <h1 className="text-4xl md:text-5xl font-extrabold text-pink-600 mb-2 tracking-tight">Discover Our Collection</h1>
          <p className="text-lg text-gray-600">Shop the latest jewelry pieces, crafted for every occasion.</p>
        </div>
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-8 gap-4">
          <input
            type="text"
            placeholder="Search by name..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="p-3 border border-gray-300 rounded-lg w-full md:w-1/3 focus:outline-none focus:ring-2 focus:ring-pink-400"
          />
          <Link href="/add-product" className="bg-pink-600 text-white px-6 py-3 rounded-lg font-semibold shadow hover:bg-pink-700 transition">+ Add Product</Link>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8">
          {filtered.length === 0 && (
            <div className="col-span-full text-center text-gray-400 text-lg">No products found.</div>
          )}
          {filtered.map((product) => (
            <div key={product.id} className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-2xl transition border border-gray-100 flex flex-col">
              <img src={product.imageUrl} alt={product.name} className="w-full h-56 object-cover" />
              <div className="p-4 flex-1 flex flex-col justify-between">
                <div>
                  <h2 className="text-lg font-bold text-gray-900 mb-1 truncate">{product.name}</h2>
                  <p className="text-pink-600 font-semibold text-xl mb-2">â‚¹{product.price}</p>
                </div>
                <Link href={`/products/${product.id}`} className="mt-2 inline-block text-pink-600 hover:underline font-medium">View Details</Link>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
